/* Block */
.breadcrumbs-section {
  padding-top: 70px;
  padding-bottom: 50px;
}
.breadcrumbs-section .breadcrumb {
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item {
  position: relative;
  font-size: 14px;
  font-weight: 500;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item.active {
  color: #2B478B;
}
.breadcrumbs-section .breadcrumb a {
  color: #767676;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item {
  padding-left: 30px;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "";
  position: absolute;
  top: calc(50% - 4px);
  left: calc(15px - 4px);
  width: 5px;
  height: 8px;
  background-image: url(../../assets/img/svg/angle-right.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 991px) {
  .breadcrumbs-section {
    padding-top: 20px;
  }
}
/* Form */
.step {
  display: none;
}
.step .step-title {
  font-size: 42px;
  font-weight: 600;
  margin-bottom: 100px;
}
.step .step-subtitle {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 80px;
}
.step input.invalid {
  border-color: #FF0000;
}
.step .invalid-message {
  margin-top: 5px;
  color: #FF0000;
}

.step.show {
  display: block;
}

.final-step {
  position: relative;
  margin-bottom: 100px;
}
.final-step::before {
  content: "";
  position: absolute;
  left: -130px;
  top: 130px;
  width: 1778px;
  height: 513px;
  background-image: url(../../assets/img/svg/picture/final-step-bg.svg);
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
}
.final-step .success-message {
  max-width: 450px;
  margin-left: auto;
}
.final-step .success-message svg {
  margin-bottom: 30px;
}
.final-step .success-message .message {
  font-size: 24px;
}

.order-form .row {
  margin: 0 -10px;
}
.order-form .form-group {
  padding: 0 10px;
  margin-bottom: 60px;
}
.order-form input[type=date]::-webkit-inner-spin-button,
.order-form input[type=date]::-webkit-calendar-picker-indicator {
  opacity: 0;
}
.order-form input:disabled {
  opacity: 0.7;
}
.order-form .btn-primary-custom {
  width: 100%;
  height: 48px;
}
.order-form .custom-radio-label {
  padding: 12px 0 12px 50px;
  width: 100%;
  background-color: #D5E7F7;
  border-radius: 6px;
  color: #343A3F;
}
.order-form .custom-radio-label::after {
  top: calc(50% - 8px);
  left: calc(25px - 8px);
  border-color: #343A3F;
}
.order-form .custom-radio-label::before {
  top: calc(50% - 4px);
  left: calc(25px - 4px);
  background-color: transparent;
}

@media (min-width: 992px) {
  .final-step .step-title {
    margin-bottom: 200px;
  }

  .order-form .col-lg-2 {
    flex: 0 0 20%;
    max-width: 20%;
  }
}
@media (min-width: 576px) {
  .order-form .first-row .form-group {
    margin-bottom: 20px;
  }
}
@media (max-width: 1199px) {
  .final-step::before {
    left: -230px;
    top: 150px;
    width: 1600px;
    height: 430px;
  }
}
@media (max-width: 991px) {
  .final-step::before {
    left: -155px;
    top: 90px;
    width: 1150px;
    height: 310px;
  }
  .final-step .success-message {
    max-width: 360px;
  }
  .final-step .success-message svg {
    width: 35px;
    margin-bottom: 15px;
  }
  .final-step .success-message .message {
    font-size: 20px;
  }
}
@media (max-width: 767px) {
  .final-step {
    margin-bottom: 0;
  }
  .final-step::before {
    display: none;
  }
  .final-step .success-message {
    max-width: 100%;
  }

  .step .step-title {
    font-size: 28px;
    margin-bottom: 40px;
  }
  .step .step-subtitle {
    font-size: 20px;
    margin-bottom: 40px;
  }
}
@media (max-width: 575px) {
  .order-form .form-group {
    margin-bottom: 30px;
  }
  .order-form .btn-prev, .order-form .btn-next, .order-form button[type=submit] {
    margin-top: 20px;
  }
}